*,
::before,
::after {
    box-sizing: border-box;
}

.header {
    display: flex;
    position: fixed;
    top: 0;
    left: 0;

    height: 60px;
    width: 100%;
    background-color: #2196F3;

    align-items: center;
    justify-content: center;

    z-index: 2;

}
.input {
    height: 25px;
    width: 150px;
    border-radius: 5px;
    border: transparent;
}
.search-button {
    height: 25px;
    border-radius: 5px;
    border: transparent;
}

.gallery-section {
    padding-top: 60px;
    padding-bottom: 30px; 
}

.gallery {
    display: flex;
    flex-wrap: wrap;
    width: 1300px;

    margin-right: auto;
    margin-left: auto;   
}

.photo-card {
position: relative;
border: 1px solid grey;
height: 275px;
width: calc((100% - 45px) / 4);
margin-right: 15px;
margin-bottom: 15px;
z-index: 1;
}

.info {
    position: absolute;
    left: 0;
    bottom: 0;

    display: flex;
    justify-content: space-evenly;
   
    width: 100%;
    padding-left: 5px;
    padding-right: 5px;   
}

.info-item {
    font-size: 15px;
}

span {
    text-align: center;
    font-weight: 400;
   
}

.item-title {
    font-weight: 700;
}

.info-item {
    display: flex;
    flex-direction: column;
}

img {
    width: 100%;
    height: 200px;
}

.load-more {
   position: relative;
   bottom: 0px;
   left: 50%;
   transform: translateX(-50%);
}

.invisible {
    display: none;
}

.photo-card:nth-child(4n) {
    margin-right: 0;
}

.load-more {
    color: white;
    width: 90px;
    height: 30px;
    border-radius: 5px;
    border: transparent;
    background-color: #2196F3 ;
}